
/* Add your styles here */
body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.app {
    text-align: center;
}

.app-logo {
    height: 40vmin;
    pointer-events: none;
}

.app-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.app-link {
    color: #61dafb;
}
